		// 	console.log(url)
		// 	let userToken = this.$db.get('userToken')
		// 	if (!userToken) {
		// 		common.jumpToLogin()
		// 		return false
		// 	} else {
		// 		this.$common.navigateTo(url)

		// 	}
		// },
	},
	computed: {
		// 获取店铺联系人手机号
		shopMobile(){
			return this.$store.state.config.shop_mobile || 0;
		},
		invoice_switch() {
			return this.$store.state.config.invoice_switch || 2;
		},
		store_switch() {
			return this.$store.state.config.store_switch || 0;
		}
	},
	watch: {}
}
